//
function strFormat(str) {
	return str < 10 ? `0${str}` : str
}
// 
export function currentTime() {
	let myDate = new Date();
	let y = myDate.getFullYear()
	let m = myDate.getMonth() + 1;
	m = m < 10 ? '0' + m : m;
	let d = myDate.getDate();
	d = d < 10 ? ('0' + d) : d;
	let time = y + '-' + m + '-' + d;
	return time
}

//
export function timeStamp(time) {
	const dates = new Date(time)
	const year = dates.getFullYear()
	const month = dates.getMonth() + 1
	const date = dates.getDate()
	const day = dates.getDay()
	const hour = dates.getHours()
	const min = dates.getMinutes()
	const days = ['', '', '', '', '', '', '']
	return {
		allDate: `${year}/${strFormat(month)}/${strFormat(date)}`,
		date: `${strFormat(year)}-${strFormat(month)}-${strFormat(date)}`, // 07-01
		day: `${days[day]}`, //  
		hour: strFormat(hour) + ':' + strFormat(min) + ':00' // 08:00
	}
}

//7
export function dateData() {
	const time = []
	const date = new Date()
	
	const now = date.getTime() //
	let timeStr = 3600 * 24 * 1000 //
	for (let i = 0; i < 7; i++) {
		const timeObj = {}
		timeObj.date = timeStamp(now + timeStr * i).date //
		// console.log(timeObj.date,'444444444')
		timeObj.timeStamp = now + timeStr * i //
		if (i == 0) {
			timeObj.week = ''
		} else if (i == 1) {
			timeObj.week = ''
		} else if (i == 2) {
			timeObj.week = ''
		} else {
			timeObj.week = timeStamp(now + timeStr * i).day
		}
		time.push(timeObj)
	}
	return time
}

//
export function timeData(startTime = '10:00:00', endTime = '21:00:00', timeInterval = 1) {
	const time = []
	const date = timeStamp(Date.now()).allDate
	const startDate = `${date} ${startTime}`
	const endDate = `${date} ${endTime}`
	const startTimeStamp = new Date(startDate).getTime()
	const endTimeStamp = new Date(endDate).getTime()
	const timeStr = 3600 * 1000 * timeInterval
	for (let i = startTimeStamp; i <= endTimeStamp; i = i + timeStr) {
		const timeObj = {}
		timeObj.time = timeStamp(i).hour
		timeObj.disable = 0
		time.push(timeObj)
	}
	return time
}

