import Vuex from 'vuex'
export default function initModal(v) {
  // VuexstoreVue
  v.prototype.$modalStore = new Vuex.Store({
    state: {
		show:false,
		title:"",
		content:'',
		showCancel:true,
		cancelText:"",
		cancelColor:"#000000",
		confirmText:"",
		confirmColor:"#762cd9",
		success:null,
    },
    mutations: {
		hideModal(state) {
			// -

			if(state.hideTabBar){
				wx.showTabBar();
			}

			state.show = false
			//
			state.title = ""
			state.content = ""
			state.showCancel = true
			state.cancelText = ""
			state.cancelColor = "#000000"
			state.confirmText = ""
			state.confirmColor = "#762cd9"
			state.success = null
		},
		showModal(state,data) {
			state = Object.assign(state,data)
			// console.log(state);
			state.show = true
		},
		success(state,res) {
			let cb = state.success
			let resObj={
				cancel:false,
				confirm:false
			}
			res=="confirm"?resObj.confirm=true:resObj.cancel=true
			cb && cb(resObj)
		}
    }
  })
  // $showModalVue
  v.prototype.$showModal = function (option) { 
	if (typeof option === 'object') {

		if(option.hideTabBar){
			wx.hideTabBar();
		}

		
		v.prototype.$modalStore.commit('showModal', option)
	}else{
		throw ""+typeof option;
	}
  }
}